export default {
	data() {
		return {
			company: null,
			returnDetail: null,
			pickerCompany: {
				shipping_name: '请选择快递公司'
			},
			courierNumber: '',
			typeTxt: '',
			returnMoney: null,
			showMsg: true,
			isShowModel: false,
			showCompany:true
		}
	},
	onLoad() {
		this.returnDetail = uni.getStorageSync('returnDetail')
		this.returnDetail.addtime = this.handleTime(this.returnDetail.addtime)
		console.log(this.returnDetail)
		console.log(this.returnDetail.type)
		if (this.returnDetail.type == 0) {
			this.typeTxt = '退款'
			this.showCompany = false
		} else if (this.returnDetail.type == 1) {
			this.typeTxt = '退货'
		} else if (this.returnDetail.type == 2) {
			this.typeTxt = '换货'
		}
		this.getCompanys()
	},
	methods: {
		//获取物流公司信息
		getCompanys() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/order/addExpressInfo",
				data: {
					shop_id: uni.getStorageSync('shop_id'),
					token: uni.getStorageSync('token')
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.company = res.data.data
					}
				}
			})
		},
		//选择快递公司
		chooseCompany(e) {
			let index = e.detail.value
			this.pickerCompany = this.company[index]
		},
		showModel() {
			this.isShowModel = true
		},
		closeModel() {
			this.isShowModel = false
		},
		//扫码
		scanCode() {
			uni.scanCode({
				success: (res) => {
					console.log('条码类型：' + res.scanType);
					console.log('条码内容：' + res.result);
					this.courierNumber = res.result
				}
			});
		},
		//提交快递信息
		submitMsg() {
			if (this.pickerCompany.shipping_name == '请选择快递公司') {
				this.showToast('请选择快递公司')
				return
			} else if (this.courierNumber == '') {
				this.showToast('请填写快递单号')
				return
			}
			this.showMsg = false
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/order/addExpressInfo",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					return_goods_id: uni.getStorageSync('returnDetail').id,
					shipping_id: this.pickerCompany.shipping_id,
					shipping_code: this.courierNumber
				},
				method: 'POST',
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						uni.showToast({
							title: '信息已提交',
							icon: 'success',
							success: () => {
								setTimeout(function() {
									uni.navigateBack({
										delta: 1
									})
								}, 1000);
							}
						})
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		//处理下单时间
		handleTime(time) {
			let date = new Date(time * 1000)
			let Y = date.getFullYear().toString()
			let M = (date.getMonth() + 1).toString().padStart(2, '0')
			let D = date.getDate().toString().padStart(2, '0')
			let h = date.getHours().toString().padStart(2, '0')
			let m = date.getMinutes().toString().padStart(2, '0')
			let s = date.getSeconds().toString().padStart(2, '0')
			let newTime = Y + '-' + M + '-' + D + ' ' + h + ':' + m + ':' + s
			return newTime
		},
		//撤销退换货申请
		cancleReturn() {
			this.closeModel()
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/order/cancelReturnGoods",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					return_goods_id: uni.getStorageSync('returnDetail').id
				},
				method: 'POST',
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						uni.showToast({
							title: '已撤销申请',
							icon: 'success',
							success: () => {
								setTimeout(function() {
									uni.navigateBack({
										delta: 1
									})
								}, 1000);
							}
						})
						return
					}
					this.showToast(res.data.msg)
				}
			})
		}
	}
}
